import React from "react";
import {
  BdsGrid,
  BdsIllustration,
  BdsTypo,
  BdsButton,
} from "blip-ds/dist/blip-ds-react/components";
import './styles.scss';

export default function ServiceUnavailableError() {
  return (
    <BdsGrid height="100vh" alignItems="center">
      <BdsGrid
        xxs="12"
        alignItems="center"
        justifyContent="center"
        flexWrap="wrap"
        padding="3"
        height="fit-content"
      >
        <BdsGrid xxs="7" xs="6" sm="6" justifyContent="center">
          <BdsIllustration type="default" name="error"></BdsIllustration>
        </BdsGrid>
        <BdsGrid className="textArea" xxs="12" xs="6" direction="column" gap="8">
          <BdsGrid direction="column" gap="2">
            <BdsTypo variant="fs-40" margin={false} bold="bold">
            Esse servio est indisponvel no momento
            </BdsTypo>
            <BdsTypo>
            Estamos enfrentando problemas de comunicao com esse servio. Voc pode tentar novamente mais tarde.
            </BdsTypo>
          </BdsGrid>
          <BdsGrid className="btnArea" direction="column" gap="2">
          <BdsButton>Ir para a pgina principal</BdsButton>
          </BdsGrid>
        </BdsGrid>
      </BdsGrid>
    </BdsGrid>
  );
}